.css3check label {
  font-weight: 300;
  color: #2a2826;
  font-size: 14px;
  display: block;
  position: relative;
  margin-bottom: 10px;
  cursor: pointer;
  line-height: 25px;
}

.css3check label i {
  color: #b7bcc5;
  font-style: normal;
}

.css3check label input {
  display: block;
  position: absolute;
  opacity: 0;
}

.css3check label input + span {
  position: relative;
  padding-left: 35px;
  display: block;
}

.css3check label input + span:before {
  content: " ";
  display: block;
  width: 25px;
  height: 25px;
  border: 1px solid #dcdfe4;
  border-radius: 5px;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-transition: all 100ms ease-in;
  -moz-transition: all 100ms ease-in;
  -ms-transition: all 100ms ease-in;
  -o-transition: all 100ms ease-in;
  transition: all 100ms ease-in;
}

.css3check label input + span:after {
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\e90b";
  font-size: 12px;
  color: #80bb02;
  left: 5px;
  top: 6px;
  position: absolute;
  opacity: 0;
  -webkit-transition: all 300ms ease-in;
  -moz-transition: all 300ms ease-in;
  -ms-transition: all 300ms ease-in;
  -o-transition: all 300ms ease-in;
  transition: all 300ms ease-in;
}

.css3check label input:disabled + span {
  color: #b7bcc5;
  text-decoration: line-through;
  font-weight: 300 !important;
  cursor: default;
}

.css3check label input:disabled + span i {
  color: #b7bcc5;
}

.css3check label input:disabled + span:before {
  border-color: #dcdfe4 !important;
}

.css3check label input:disabled + span:after {
  color: #dcdfe4;
}

.css3check label input:checked + span {
  font-weight: 600;
}

.css3check label input:checked + span:before {
  border-color: #80bb02;
}

.css3check label input:checked + span:after {
  opacity: 1;
}

.css3radio__hidden {
