.rating {
  width: 120px;
  height: 21px;
}

.rating:hover label.rating__field .rating__star {
  background-image: url(../images/star.png);
}

.rating__field {
  float: right;
  width: 24px;
  height: 21px;
  position: relative;
  cursor: pointer;
}

.rating__field:after {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -2.5rem 0 0 -2.5rem;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  content: '';
  opacity: 0;
  pointer-events: none;
  box-shadow: inset 0 0 0 2.5rem #fdd600;
  z-index: 1;
}

.rating__field.is-active .rating__star, .rating__field.is-active ~ .rating__field .rating__star {
  background-image: url(../images/star-active.png);
}

.rating__radio {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}

.rating__star {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  text-indent: -9999px;
  background: url(../images/star.png) 0 0 no-repeat;
}

.rating label.rating__field:hover .rating__star, .rating label.rating__field:hover ~ .rating__field .rating__star {
  background-image: url(../images/star-active.png);
}
